import React from 'react';
import { NewsDateType } from 'components/news-block/news-date-type/news-date-type';
import { PinnedIcon } from 'components/icons/pinned';
import { Link } from 'components/shared/link/link';
import {destructDate} from "utils/functions";
import './simple-article.scss';

export function SimpleArticle({ header: title, rubrics, date, typeLink, imgSrc, isPinned, hideDate, href }) {
	const dt = destructDate(date);
	const type = rubrics?.[0]?.header || '';
  return (
    <div className="simple-article">
      <Link href={href}>
        <img className="simple-article-img img-fluid mb-2" src={imgSrc} alt={title} />
      </Link>
      <div className="d-flex align-items-center">
        {!hideDate && <NewsDateType date={`${dt.dd} ${dt.monthA} ${dt.yy} ${dt.time} `} type={type} typeLink={typeLink} className="px-0" />}
        {isPinned && <PinnedIcon />}
      </div>
      <Link href={href}>
        <h4 className="simple-article-title">{title}</h4>
      </Link>
    </div>
  );
}

SimpleArticle.defaultProps = {
  hideDate: false,
};
