import React from 'react';
import { useSelector } from 'react-redux';
import { Container, Row, Col } from 'reactstrap';
import {
  largeBlockImg,
  newsImg1,
  newsImg2,
  newsImg3,
  newsImg4,
  newsImg5,
  newsImg6,
  test1,
} from './../../../img/news';

import { NewsItem } from './../news-item/news-item';
import { More } from './../../shared/more/more';
import MainPageNewsRow from './news_row_on_main';
import './../news-block.scss';
import { NewsTestItem } from '../news-test-item/news-test-item';
import * as UNITS from "utils/const";
import {destructDate} from "utils/functions";
const type = 'Эмоции';

export function EmotionSection(props) {
	const result = useSelector(state => state.app.list.initialArticles);
	const dataList = result?.response?.data?.Sections || []
	let articlesList = [];
	let rubricUrl = '';

	if (dataList?.[2]?.articles?.length) {
		articlesList = dataList[2].articles
		rubricUrl = dataList[2].url
	}

	const mainNews = articlesList[0] || {};
	const mainNewsDt = destructDate(mainNews.date);

	function handleLoadMoreNews() {
		props.onMoreUnitToggle(UNITS.UNIT_EMO);
	}




  return (
    <Container className="py-3 news-block padding-x-20">
      <Row className="align-items-center mb-2">
        <Col xs="auto"><p className="title size-14 text-uppercase font-weight-900 mb-0">{ UNITS.UNIT_EMO_TEXT}</p></Col>
        <Col className="pl-0">
          <hr />
        </Col>
      </Row>

      <Row noGutters>
        <Col xs='auto' className="news-item-line-wrapper-large mr-15">
	        <NewsItem
		        imgSrc={ mainNews.image_m }
		        imgAlt={ mainNews.header }
		        previewText={ mainNews.header }
		        text={ mainNews.description }
		        type={ mainNews?.rubrics?.[0]?.header || ''}
		        date={`${mainNewsDt.dd} ${mainNewsDt.monthA} ${mainNewsDt.yy} ${mainNewsDt.time} `}
		        large
	        />
        </Col>
        <Col className="news-item-line-wrapper">
          <NewsTestItem
            imgSrc={test1}
            type={type}
            date="10 октября 2018 18:16"
            previewText="Тест по главным песням «Мумий Тролль»"
          />
        </Col>
      </Row>
      <Row>
        <Col className="py-3"><hr /></Col>
      </Row>
	    <MainPageNewsRow list={articlesList.slice(1)} unit={UNITS.UNIT_EMO} />

      <Row>
        <Col className="py-3">
          <More title="Больше эмоций" className="font-weight-bold" onClick={handleLoadMoreNews} />
        </Col>
      </Row>
    </Container>
  );
}

