import { Simplex } from 'react-simplex';
import dayjs from 'dayjs';
import axios from 'axios';
import { mkAxios } from './api';
import config from './config/core';

export const APP_CONF = { ...config };


// console log
export const echo = (...args) => {
	if (!APP_CONF.devEnvironment) {
		return null;
	}
	console.log.apply(this, args);
	return null;
};

/* выдать сообщение об ошибке */
export function fireError(msg = APP_CONF.unknownError) {
	// Ts(msg, 'err');
	echo('ОШИБКА: ', msg);
}

/* апдейт информации по данным в симплексе (Local Storage) */
export function simplexSet(name, data, folder = 'DataStub') {
	if (!data || !name) {
		fireError('simplexSet:: got empty data');
		return false;
	}
	Simplex.update(folder, { [name]: { ...data } });
	return true;
}

/* загрузить информацию из симплекса (Local Storage) */
export function simplexGet(name, config = {}) {
	const { folder = 'DataStub', dontError = true } = config;
	if (!Simplex || !Simplex[folder]) {
		if (!dontError) {
			fireError(`simplexGet:: no such folder [${folder}]`);
		}
		return null;
	}
	if (!Simplex[folder][name]) {
		if (!dontError) {
			fireError(`simplexGet:: no such data [${folder} / ${name}]`);
		}
		return null;
	}

	return Simplex[folder][name] || null;
}

/* проверка является ли ответ ошибкой */
export function handleError (response) {
	if (response instanceof Error) {
		throw response;
	}
	throw new Error(response.data.message || 'Unknown error');
}

/* АПИ запросов */
export function makeRequest (method, url, requestData, callback = null, okStatus = 200, getImage = false) {
	if (!window.axiosAPI) {
		mkAxios();
	}
	const ax = window.axiosAPI;
	const requestParams = { method, url };
	if (Object.keys(requestData).length) {
		requestParams.data = requestData;
	}

	return new Promise(resolve => {
		if (okStatus === 201 && getImage) { // если надо получить картинку в base64
			requestParams.responseType = 'arraybuffer';
		}
		ax(requestParams).then(response => {
			const { connectError = false, status, data = {} } = response;
			const error = status !== okStatus;

			if (callback && typeof callback === 'function') {
				callback(response);
			}
			resolve({
				status, // статус ответа
				connectError, // ошибка сети
				error, // ошибка (если статус ответа !== okStatus)
				data, // данные
				requestDt: dayjs(), // время ответа
			});
		}).catch(handleError);
	}); // end of Promise
}

const remasterTextDate = date => {
	if (!date) return null;
	const dtText = date.split(/[-.,/ ]+/);
	if (dtText.length >= 3) {
		const validDate = dayjs(new Date(dtText[2], parseInt(dtText[1], 10) - 1, dtText[0])); // create date in YYYY.MM.DD
		return validDate;
	}
	return null;
};

// раскладывает полученную дату на запчасти и возвращает ее в виде объекта с доп данными
export const destructDate = masterDate => {
	if (!dayjs(masterDate).isValid()) {
		const tmp = remasterTextDate(masterDate);
		if (tmp === null) {
			return { dt: 'INVALID_DATE' };
		} else {
			// eslint-disable-next-line
			masterDate = tmp;
		}
	}

	const now = dayjs();
	const dayNames = ['Воскресенье', 'Понедельник', 'Вторник', 'Среда', 'Четверг', 'Пятница', 'Суббота']; //  тк в dayjs 0 это воскресенье
	const dayNamesShort = ['Вс', 'Пн', 'Вт', 'Ср', 'Чт', 'Пт', 'Сб'];
	const monthsNames = [
		'Январь',
		'Февраль',
		'Март',
		'Апрель',
		'Май',
		'Июнь',
		'Июль',
		'Август',
		'Сентябрь',
		'Октябрь',
		'Ноябрь',
		'Декабрь',
	];
	const monthsNamesA = [
		'Января',
		'Февраля',
		'Марта',
		'Апрель',
		'Мая',
		'Июня',
		'Июля',
		'Августа',
		'Сентября',
		'Октября',
		'Ноября',
		'Декабря',
	];
	const monthsNamesShort = ['Янв', 'Фев', 'Мар', 'Апр', 'Май', 'Июн', 'Июл', 'Авг', 'Сен', 'Окт', 'Ноя', 'Дек'];
	const currentDay = dayjs(masterDate);
	const retVal = {
		dt: currentDay.format('DD.MM.YYYY'),
		yy: currentDay.format('YYYY'),
		mm: currentDay.format('MM'),
		dd: currentDay.format('DD'),
		time: currentDay.format('HH:mm'),
		h: currentDay.format('HH'),
		m: currentDay.format('mm'),
		s: currentDay.format('ss'),
		fullTime: currentDay.format('HH:mm:ss'),
		day: dayNames[currentDay.day()],
		dayShort: dayNamesShort[currentDay.day()],
		month: monthsNames[currentDay.month()], // month начинается с 0
		monthA: monthsNamesA[currentDay.month()],
		monthShort: monthsNamesShort[currentDay.month()],
	};
	const masterYY = parseInt(currentDay.format('YYYY'), 10);
	const masterMM = parseInt(currentDay.format('MM'), 10);
	const masterDD = parseInt(currentDay.format('DD'), 10);

	const nowYY = parseInt(now.format('YYYY'), 10);
	const nowMM = parseInt(now.format('MM'), 10);
	const nowDD = parseInt(now.format('DD'), 10);

	retVal.wordDate = null;
	retVal.isYY = masterYY === nowYY; // этот ли год
	retVal.isMM = masterMM === nowMM; // этот ли месяц
	retVal.isDD = masterDD === nowDD; // этот ли день
	retVal.isToday = retVal.isYY && retVal.isMM && retVal.isDD; //  дата == сегодня

	if (retVal.isYY && retVal.isMM) {
		const diff = nowDD - masterDD;
		if (diff === 0) {
			retVal.wordDate = APP_CONF.dateWords.TODAY;
			retVal.wordDateFor = APP_CONF.dateWords.FOR_TODAY;
		} else if (diff === -1) {
			retVal.wordDate = APP_CONF.dateWords.TOMORROW;
			retVal.wordDateFor = APP_CONF.dateWords.FOR_TOMORROW;
			retVal.isTomorrow = true;
		} else if (diff === -2) {
			retVal.wordDate = APP_CONF.dateWords.AFTERTOMORROW;
			retVal.wordDateFor = APP_CONF.dateWords.FOR_AFTERTOMORROW;
		} else if (diff === 1) {
			retVal.wordDate = APP_CONF.dateWords.YESTERDAY;
			retVal.wordDateFor = APP_CONF.dateWords.FOR_YESTERDAY;
			retVal.isYesterday = true;
		} else if (diff === 2) {
			retVal.wordDate = APP_CONF.dateWords.BEFOREYESTERDAY;
			retVal.wordDateFor = APP_CONF.dateWords.FOR_BEFOREYESTERDAY;
		}
	}
	return retVal;
};

// сравнить 2 даты
// fullDate = true/false - сравнить включая поминутно
export const compareDates = (a, b, fullDate = false) => {
	if (!a || !b) {
		return false;
	}
	if (!dayjs(a).isValid()) {
		const tmp = remasterTextDate(a);
		if (tmp === null) {
			return false;
		} else {
			// eslint-disable-next-line
			a = tmp;
		}
	}
	if (!dayjs(b).isValid()) {
		const tmp = remasterTextDate(b);
		if (tmp === null) {
			return false;
		} else {
			// eslint-disable-next-line
			b = tmp;
		}
	}
	if (fullDate) {
		return dayjs(a).format('DD.MM.YYYY HH:mm') === dayjs(b, 'DD-MM-YYYY').format('DD.MM.YYYY HH:mm');
	}

	return dayjs(a).format('DD.MM.YYYY') === dayjs(b, 'DD-MM-YYYY').format('DD.MM.YYYY');
};
