import { createReducer } from 'redux-act';
import { combineReducers } from 'redux';
import { connectRouter } from 'connected-react-router';
import * as actions from './actions';
import { InitialList, InitialUser } from './initial_data';

const initialState = {
  user: new InitialUser(), // информацию по пользователю системы
  data: {}, // данные формы (информация по заказу)
  dataChanged: false, // данные формы изменились
  list: {
    // списки с данными заглушками
    initialData: new InitialList(),
	  initialArticles: new InitialList(),
  },
  showModal: {}, // показать модальное окно
  searchFilter: {
    searchText: '',
  },
  searchRef: null,
};

const rootReducer = createReducer(
  {
    [actions.initialAppData]: (state, payload) => ({
	    ...state,
	    list: {
		    ...state.list,
		    initialData: payload,
	    },
    }),
    [actions.initialAppArticles]: (state, payload) => ({
	    ...state,
	    list: {
		    ...state.list,
		    initialArticles: payload,
	    },
    }),
  },
  initialState,
);

export default history =>
  combineReducers({
    router: connectRouter(history),
    app: rootReducer,
  });
