import React, { useState, useRef } from 'react';
import { Container, Row, Col } from 'reactstrap';
// import { cloneDeep } from 'lodash';
import { useSelector } from 'react-redux';
import { NewsItem } from './../news-item/news-item';
import { NewsItemMain } from './../news-item-main/news-item-main';
import { NewsSimpleItem } from './../news-simple-item/news-simple-item';
import { More } from './../../shared/more/more';
import { destructDate } from "utils/functions";
import * as UNITS from "utils/const";
// import { STUB_DATA, STUB_AUTHOR_COLOGNE_DATA } from './stub-data';
import MainPageNewsRow from './news_row_on_main';
import './../news-block.scss';


const NEWS_IN_ROW = 3;

export function LifeSection({ isMobile, onMoreUnitToggle }) {
	const [ newsBlocs, setNewsBlocs ] = useState(2);
	const realNewsBlocs = useRef(newsBlocs)
	const result = useSelector(state => state.app.list.initialArticles);
	const dataList = result?.response?.data?.Sections || []
	let articlesList = [];
	let rubricUrl = '';

	if (dataList?.[0]?.articles?.length) {
		articlesList = dataList[0].articles
		rubricUrl = dataList[0].url
	}

	function renderNewsRightColumn(item, index) {
		const dt = destructDate(item.date);
		return (
			<React.Fragment key={index}>
				<NewsSimpleItem
					previewText={item.header}
					type={ item?.rubrics?.[0]?.header || '' }
					date={`${dt.dd} ${dt.monthA} ${dt.yy} ${dt.time} `}
				/>
				{
					index !== 2 && <hr />
				}
			</React.Fragment>
		);
	}


	function handleLoadMoreNews() {
		onMoreUnitToggle(UNITS.UNIT_LIFE);
	}

  // const NEWS_DATA = cloneDeep(STUB_DATA);
	const mainNews = articlesList[0] || {};
	const mainNewsDt = destructDate(mainNews.date);

  return (
    <Container className="py-3 news-block padding-x-20">
      <Row className="align-items-center mb-2">
        <Col xs="auto"><p className="title size-14 text-uppercase font-weight-900 mb-0">{ UNITS.UNIT_LIFE_TEXT }</p></Col>
        <Col className="pl-0">
          <hr />
        </Col>
      </Row>

      <Row noGutters>
        <Col xs='auto' className="news-item-line-wrapper-large mr-15">
          <NewsItemMain
	          imgSrc={ mainNews.image_m }
		        imgAlt={ mainNews.header }
		        imgPlaceholder={ mainNews.header }
		        previewText={ mainNews.description }
		        type={ mainNews?.rubrics?.[0]?.header || ''}
	          date={`${mainNewsDt.dd} ${mainNewsDt.monthA} ${mainNewsDt.yy} ${mainNewsDt.time} `}
	          large
          />
        </Col>
        <Col className="news-item-line-wrapper">
	        {
		        articlesList.slice(1,4).map(renderNewsRightColumn)
	        }
        </Col>
      </Row>
      <Row>
        <Col className="py-3"><hr /></Col>
      </Row>

	    <MainPageNewsRow list={articlesList.slice(4)} unit={UNITS.UNIT_LIFE} />

      <Row>
        <Col className="pt-3">
          <More title="Больше жизни" className="font-weight-bold" onClick={handleLoadMoreNews} />
        </Col>
      </Row>
    </Container>
  );
}

