import React  from 'react';
import * as PropTypes from 'prop-types';
import {destructDate} from "utils/functions";
import {Col, Row} from "reactstrap";
import {NewsItem} from "components/news-block/news-item/news-item";



function MainPageNewsRow(props) {
	const { list, perRow: NEWS_IN_ROW, rows, unit} = props;



	function renderOneBottomNewsBloc(item, index) {
		const dt = destructDate(item.date);
		return (
			<Col key={index} className={`news-item-line-wrapper ${index !== 2 ? 'mr-15' : ''}`}>
				<NewsItem
					imgSrc={item.image_b}
					imgAlt={item.header}
					previewText={item.header}
					type={ item?.rubrics?.[0]?.header || '' }
					date={`${dt.dd} ${dt.monthA} ${dt.yy} ${dt.time} `}
				/>
			</Col>
		);
	}

	function renderBottomNewsBlocs() {
		let res = [];
		for(let i = 0; i < rows; i++) {
			if (list.length > i * NEWS_IN_ROW) {
				if (res.length) {
					res.push(<Row key={`hr${i}`} ><Col className="py-3"><hr /></Col></Row>);
				}
				res.push(<Row key={i} noGutters>{list.slice(i * NEWS_IN_ROW, i * NEWS_IN_ROW + NEWS_IN_ROW).map(renderOneBottomNewsBloc)}</Row>);
			}
		}
		return res
	}



	return renderBottomNewsBlocs();
}

MainPageNewsRow.propTypes = {
	list: PropTypes.arrayOf(PropTypes.object).isRequired,
	rows: PropTypes.number, // сколько строк
	perRow: PropTypes.number, // сколько новостей в строку
	unit: PropTypes.string.isRequired, // блок (жизнь, знания, эмоции)
};

MainPageNewsRow.defaultProps = {
	perRow: 3,
	rows: 2,
};

export default React.memo(MainPageNewsRow)
