import React, {useEffect, useState} from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { BrowserRouter as Router, Route } from "react-router-dom";
import Main from './pages/main';
import ScrollToTop from './components/shared/scroll-to-top/scroll-to-top';
import { initialAppData, initialAppArticles } from 'store/actions';
import newsController from 'utils/controllers/news_controller';
import './app.scss';

function App() {
	const dispatch = useDispatch();

	useEffect(() => {
		let initialData = {};
		let initialArticles = {};

		async function fetchData() {
			initialData = await newsController.initialGetData();
			initialArticles = await newsController.initialGetArticles();
			console.log(">>>> RES = ", initialData, initialArticles)
			dispatch({
				type: initialAppData.getType(),
				payload: { response: initialData, loading: false },
			});
			dispatch({
				type: initialAppArticles.getType(),
				payload: { response: initialArticles, loading: false },
			});

		} // end of function

		dispatch({
			type: initialAppData.getType(),
			payload: { loading: true },
		});
		dispatch({
			type: initialAppArticles.getType(),
			payload: { loading: true },
		});

		fetchData();

	}, []);

  return (
    <Router basename={process.env.PUBLIC_URL}>
      <ScrollToTop>
        <Route component={Main} />
      </ScrollToTop>
    </Router>
  );
}

export default App;
