import React from 'react';
import { useSelector } from 'react-redux'
import { VerticalCarousel } from 'components/shared/vertical-carousel/vertical-carousel';

// STUB DATA
import { HEADER_NEWS_DATA } from './header-news-data';
import {echo} from "utils/functions";

function HeaderNewsGenerall(props) {
	const result = useSelector(state => state.app.list.initialData);
	const dataList = result?.response?.data?.SubjectsList || []
	echo(">>> MENU data ", dataList, result?.response?.data?.SubjectsList);
  return (
    <VerticalCarousel data={dataList} onTabChange={props.onThemeChange} />
  );
}

export default React.memo(HeaderNewsGenerall)
