import {makeRequest} from "utils/functions";

class newsController {
	/* первичные данные при загрузке приложения */
	initialGetData(requestData = {}, callback) {
		return makeRequest('get', '/api/v1/extra.json', requestData, callback);
	} // end of initialGet

	/* первичные данные при загрузке приложения */
	initialGetArticles(requestData = {}, callback) {
		return makeRequest('get', 'api/v1.json', requestData, callback);
	} // end of initialGet

}

export default new newsController();
