import { Simplex } from 'react-simplex';

/* инициализация Simplex */
export function simplexInit() {
	if (!Simplex.User && !Simplex.Pages && !Simplex.Global) {
		Simplex.setStorageDriver(localStorage, false);
		Simplex.init('User', {}, true); // инфа о пользователе
		Simplex.init('Pages', {}, true); // конфиги на страницах
		Simplex.init('Global', {}, true); // глобальные конфиги
		Simplex.init('DataStub', {}, true); // заглушки с данными (пока без бека)
	}
}

